* {
    margin: 0px;
    padding: 0px;
    color:white;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
    
}

*::-webkit-scrollbar {
    display: none;
}

:root {
    --monst: 'Montserrat', sans-serif;
    --comforta:'Comfortaa', cursive;
    --lightgold: #EDD087;
    --medgold:  rgba(218, 165, 32,0.5);
    --darkgold: #7C5E13;
    --orange: #FF9100;
    --transblack: rgb(0,0,0,0.5);
}

h1,h2,h3,h4,h5,h6 {
    margin: 0px;
    color: white;
    font-family: var(--monst);
}

p{
    margin:0px;
}

body {
    background-image: url('../img/background2.jpg');
}

input , textarea {
    border-radius: 8px;
    height: 30px;
}

textarea {
    overflow: scroll;
}

img {
    object-fit: cover;
}
nav {
    margin-top: 20px;
    background-color: black;
    width: 100%;
    height: 90px;
    border-top: var(--darkgold) 1px solid;
    border-bottom: var(--darkgold) 1px solid;
    align-items: center;;
    
}

.link {
    color: var(--orange)
}

i.fa-linkedin {
    color: #0077B5
}

i.fa-github {
    color:gray;
}


.flex {
    display:flex;
}

.display_none {
    display:none;
}

.monst {
    font-family: var(--monst);
}

.comforta {
    font-family: var(--comforta)
}


button {
    border-radius: 8px;
    background-color: var(--orange); 
    border: none;
    border: 2px solid var(--orange  ); 
    color: var(--transblack);
    background-image: linear-gradient(#FEDF90, var(--orange));
    width: 155px;
    height:30px;
    font-weight: bold;
}

button.voted {
    background-color: black;
    border: 2px solid rgba(255, 144, 0,.2); 
    background-image: linear-gradient(rgba(254, 223, 144,0.5), rgba(255, 144, 0,.5));
}

.film_strip{
    background-color: var(--transblack);
    border-top: 5px dashed var(--orange);
    border-bottom: 5px dashed var(--orange);
    min-height: 350px;
    margin: auto;
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 30px;
    margin-bottom: 50px;
}

.wrapper {
    width: 80%;
    margin:auto;
}

.edit_body {
    min-height: 550px;
}

@media (max-width: 1300px){

    .wrapper{
        width: 80%;
    }
}

@media (max-width: 800px){

    .wrapper{
        width: 90%;
    }
}

/************************/
/* BREAKPOINT 700px    
/************************/
@media (max-width: 450px){
    .film_strip {
        padding: 40px 0px;
        width: 100%;
    }


}